/* Overlay Styles */
.custom-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 999; /* Higher z-index value */
  }
  
  /* Modal Styles */
  .custom-modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    max-width: 80%;
    z-index: 1000; /* Higher z-index value than the overlay */
  }
  
  /* Modal Content Styles */
  .modal-content {
    text-align: left;
    font-family: Arial, sans-serif;
  }
  
  .modal-content h3 {
    font-size: 24px;
    margin-bottom: 10px;
    color: #333;
  }
  
  .modal-content p {
    font-size: 16px;
    margin: 10px 0;
    color: #555;
  }
  
  /* Close Button Styles */
  .modal-content button {
    background-color: #d9534f;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 20px;
    font-size: 16px;
  }
  
  /* Close Button Hover Effect */
  .modal-content button:hover {
    background-color: #c9302c;
  }
  